  useEffect( async () => {
    imagesShallow = [];
    setLoading(false);
   const images = await getImages();
    if(!images) {
      setLoading(true);
      return;
    };
    for(let img = 0; img < 5; img++) {
    // for (let img of Object.values(images)) { // promenio sam na klasican loop da bih ogranicio broj slika koji se fetchuje.
      const newLink = {
            src: Object.values(images)[img].src,
            width: Object.values(images)[img].width,
            height: Object.values(images)[img].height,
            class: "images",
            type: Object.values(images)[img].type          
          };
          imagesShallow.push(newLink);             
    };
    shuffleArray(imagesShallow);
    setPhotos(imagesShallow); 
    setLoading(true);
    setFilterLoading(true);

    //Get Videos

    
  }, []);

  const getVideos = async () => {
    const res = await fetch(`${serverUrl}/videos.json`);
    const data = await res.json();
    return data;
  };
  
